.chart {
  position: relative;
  height: 13rem;
}

.chart svg.defs {
  width: 0;
  height: 0;
  visibility: hidden;
}

.chart, .chart div {
  box-sizing: border-box;
}

.chart text, .chart span {
  cursor: default;
}

.chart.chart--score-progress {
  position: relative;
  max-height: 144px;
}

.chart.chart--score-progress text, .chart.chart--score-progress span {
  cursor: default;
}

.chart.chart--score-progress .tooltip {
  position: absolute;
  top: 50px;
  left: 50%;
  background-color: #F5F9FC;
  width: 150px;
  box-shadow: 0 6px 15px rgba(0, 0, 0, 0.1);
  visibility: hidden;
}

.chart.chart--score-progress .tooltip:after {
  content: "";
  display: table;
  clear: both;
}

.chart.chart--score-progress .tooltip.show {
  visibility: visible;
}

.chart.chart--score-progress .tooltip.negative .numeric {
  color: #e74c3c;
}

.chart.chart--score-progress .tooltip .numeric {
  position: relative;
  float: left;
  width: 40%;
  color: #3498DB;
  font-size: 18px;
  font-size: 1.28571429rem;
  font-weight: bold;
  padding: 4px 10px 4px 0;
  text-align: center;
}

.chart.chart--score-progress .tooltip .numeric .unit {
  position: relative;
  display: inline-block;
  top: -0.5em;
  font-size: 70%;
}

.chart.chart--score-progress .tooltip .text {
  float: left;
  width: 60%;
  color: #6d7e95;
  font-size: 12px;
  font-size: 0.85714286rem;
  text-transform: uppercase;
  font-weight: bold;
  line-height: 1.2;
  background-color: #fff;
}

.chart.chart--score-progress .tooltip .text span {
  display: block;
}

.chart.chart--score-progress .tooltip .numeric, .chart.chart--score-progress .tooltip .text {
  padding: 15px;
}

.chart.chart--score-progress .tooltip.tooltip-score {
  cursor: default;
}

.chart.chart--score-progress .tooltip.tooltip-score:after {
  content: "";
  display: table;
  clear: both;
}

.chart.chart--score-progress .tooltip.tooltip-score .score-value {
  float: left;
  font-size: 14px;
  font-size: 1rem;
  font-weight: 500;
  padding: 7px;
  color: #6d7e95;
  background-color: #fff;
  width: 100%;
  text-align: center;
  text-transform: uppercase;
}

.chart.chart--score-progress .tooltip.tooltip-score .score-currency {
  float: left;
  font-size: 17px;
  font-size: 1.21428571rem;
  font-weight: 600;
  padding: 7px;
  text-align: center;
  color: #6d7e95;
  width: 100%;
}

.chart.chart--score-progress svg.graph .axis .domain {
  display: none;
}

.chart.chart--score-progress svg.graph .axis.axis--x .tick line {
  stroke: #b0bac8;
  stroke-dasharray: 6, 20;
}

.chart.chart--score-progress svg.graph .axis.axis--x .tick.tick--median line {
  stroke-dasharray: 15, 15;
}

.chart.chart--score-progress svg.graph .axis.axis--x .tick.tick--major line {
  stroke-dasharray: 16, 20;
}

.chart.chart--score-progress svg.graph .axis.axis--x .tick.tick--minor text {
  display: none;
  font-size: 14px;
  font-size: 1rem;
}

.chart.chart--score-progress svg.graph .axis.axis--x .tick text {
  text-anchor: start;
  font-size: 10px;
  font-size: 0.71428571rem;
  font-weight: bold;
  fill: #b0bac8;
  text-transform: uppercase;
}

.chart.chart--score-progress svg.graph .axis.axis--x .tick:last-of-type text {
  display: none;
}

.chart.chart--score-progress svg.graph .axis.axis--x .tick:first-of-type text {
  display: block;
}

.chart.chart--score-progress svg.graph .axis.axis--y .tick:first-of-type line {
  display: none;
  stroke: #b6daf2;
